import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Header from '../PartialViews/section-header';
import Hidden from '@material-ui/core/Hidden';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import EcoIcon from '@material-ui/icons/Eco';
import SpeedIcon from '@material-ui/icons/Speed';
import HeadsetMicIcon from '@material-ui/icons/HeadsetMic';
import SecurityIcon from '@material-ui/icons/Security';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import MovieIcon from '@material-ui/icons/Movie';
import AndroidIcon from '@material-ui/icons/Android';
import AppleIcon from '@material-ui/icons/Apple';
import PersonIcon from '@material-ui/icons/Person';
import Anchor from '@material-ui/core/Link';

import ModalVideo from 'react-modal-video';
import BenefitsTab from '../PartialViews/benefits-tab';
import Carousel from 'react-material-ui-carousel';

import logo from '../../static/images/features/app.png';
import app1 from '../../static/images/features/img1.png';
import app2 from '../../static/images/features/img2.png';
import img3 from '../../static/images/mac.png';

const useStyles = makeStyles((theme) => ({
    section: {
        minHeight: "100vh"
    },
    sectionHeader: {
        backgroundColor: theme.palette.background.soft,
        color: theme.palette.text.light,
        borderRadius: "20px",
        padding: "6px 30px"
    },
    sectionTitle: {
        color: theme.palette.text.soft
    },
    icon: {
        backgroundImage: "linear-gradient(0deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "15px"
    },
    button: {
        borderRadius: "30px",
        padding: "7px 50px",
        border: `2px solid ${theme.palette.text.light}`,
        color: theme.palette.text.light
    },
    videoSection: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white"
    },
    buttonContained: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "10px 30px",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonOutlined: {
        color: "#4a148c",
        padding: "10px 30px",
        border: "1px solid #4a148c",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonIcon: {
        marginRight: "3px"
    }
}));

const handleClick = (e) => {
    e.preventDefault();
    const anchor = document.querySelector(e.currentTarget.getAttribute("href"));
    
    if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
};

export default function Home() {
    
    const classes = useStyles();
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up("sm"));
    const bmr = () => {
        if(matches) {
            return "20px";
        }

        return "0";
    }
    const fs = () => {
        if(matches) {
            return "h3";
        }

        return "h5";
    }

    const tc = () => {
        if(matches) {
            return "left";
        }

        return "center";
    }

    return (
        <>
            <Header>
                <Typography 
                    variant={fs()}
                    gutterBottom
                    style={{ 
                        marginLeft: "-3px", 
                        color:"white", 
                        animatinoDelay:"0.2s", 
                        MozAnimationDelay: "0.2s", 
                        WebkitAnimationDelay: "0.2s"}} 
                    className={`${"wow fadeInRight"}`}>
                        <Box textAlign={tc()} fontWeight="bold">Catch a ride heading your way with Along.ng</Box>
                </Typography>
                <Typography 
                    variant="subtitle1" 
                    style={{color:"white", animatinoDelay:"0.3s", MozAnimationDelay: "0.3s", WebkitAnimationDelay: "0.3s"}} 
                    className={`${"wow fadeInRight"}`}>
                        <Box textAlign={tc()}>Find affordable and secure rides with the best fare rates!</Box>
                </Typography>
                <Box mt={3} textAlign={tc()}>
                    <Button 
                        variant="contained"
                        href="#app-features" 
                        onClick={handleClick}
                        className={`${classes.button} ${"wow rubberBand"}`} 
                        style={{marginRight: bmr(), color: theme.palette.text.main, backgroundColor: theme.palette.background.default, animatinoDelay:"0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s"}}
                        disableElevation
                        ><b>Learn more</b></Button>
                    <Hidden smUp>
                        <Box display="block" mb={2}></Box>
                    </Hidden>
                    <Button 
                        variant="outlined"
                        color="inherit"
                        href="#app-downloads"
                        onClick={handleClick}
                        className={`${classes.button} ${"wow rubberBand"}`}
                        style={{animatinoDelay:"0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s"}}
                        disableElevation
                        >Get Started</Button>
                </Box>
            </Header>
            <Features />
            <Video />
            <Benefits />
            <Testimonial />
            <Download />
            <Contact />
        </>
    )
}

function Features() {
    const classes = useStyles();
    return (
        <>
            <Container id="app-features" className={classes.section} style={{backgroundColor:"#f6fbfd"}}>
                <Grid container justify="center">
                    <Grid item>
                        <Box my={8} textAlign="center">
                            <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                <Box fontWeight="bold" letterSpacing={1}>Features</Box>
                            </Button>
                            <Typography variant="h5" className={`${classes.sectionTitle} ${"wow fadeIn"}`}>
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Amazing Features</Box>
                            </Typography>
                        </Box>
                    </Grid>
                </Grid>
                <Grid container justify="center" spacing={2}>
                    <Grid item xs={11} md={4}>
                        <Grid container spacing={2} justify="flex-end">
                            <Grid item xs={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="right">
                                        User Friendly
                                    </Box>
                                </Typography>
                                <Typography gutterBottom variant="body1">
                                    <Box component="span" color="secondary" textAlign="right">
                                        Our app is easy and simple to use, just download search and make a request
                                    </Box>
                                </Typography>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton className={classes.icon}>
                                    <EcoIcon />
                                </IconButton>
                            </Grid>
                        </Grid>
                        <Grid container spacing={2} justify="flex-end">
                            <Grid item xs={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="right">
                                        Super Fast Speed
                                    </Box>
                                    
                                </Typography>
                                <Typography gutterBottom>
                                    <Box component="span" textAlign="right">
                                        Request and join a ride in minutes and get real-time notifications
                                    </Box>
                                </Typography>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton className={classes.icon}>
                                    <SpeedIcon />
                                </IconButton>
                            </Grid>
                        </Grid>
                        <Grid container spacing={2} justify="flex-end">
                            <Grid item xs={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="right">
                                        24/7 Support
                                    </Box>
                                </Typography>
                                <Typography gutterBottom>
                                    <Box component="span" textAlign="right">
                                        We provide 24/7 support to our customers via phone and email.
                                    </Box>
                                </Typography>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton className={classes.icon}>
                                    <HeadsetMicIcon />
                                </IconButton>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={11} md={3}>
                        <img src={logo} alt="Along logo" width="100%"/>
                    </Grid>
                    <Grid item xs={11} md={4}>
                        <Grid container spacing={2} justify="flex-start">
                            <Grid item xs={12} md={2}>
                                <IconButton className={classes.icon}>
                                    <SecurityIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={12} md={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="left">
                                        Secure
                                    </Box>
                                </Typography>
                                <Typography gutterBottom variant="body1">
                                    <Box component="span" color="secondary" textAlign="left">
                                        GPS and location determination with encrypted and secure communications.
                                    </Box>
                                </Typography>
                            </Grid>
                        </Grid>
                        <Grid container spacing={2} justify="flex-start">
                            <Grid item xs={12} md={2}>
                                <IconButton className={classes.icon}>
                                    <FavoriteBorderIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={12} md={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="left">
                                        Happy drivers &amp; customers
                                    </Box>
                                    
                                </Typography>
                                <Typography gutterBottom>
                                    <Box component="span" textAlign="left">
                                        Both our drivers and customers earn from the app
                                    </Box>
                                </Typography>
                            </Grid>
                            
                        </Grid>
                        <Grid container spacing={2} justify="flex-start">
                            <Grid item xs={12} md={2}>
                                <IconButton className={classes.icon}>
                                    <AccountBalanceIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={12} md={9}>
                                <Typography gutterBottom variant="h6">
                                    <Box fontWeight="bold" textAlign="left">
                                        Payment Options
                                    </Box>
                                </Typography>
                                <Typography gutterBottom>
                                    <Box component="span" textAlign="left">
                                        Pay via Bank Transfer, Naria Card or Cash Payment
                                    </Box>
                                </Typography>
                            </Grid>
                            
                        </Grid>
                    </Grid>
                </Grid>
            </Container>
            <Container className={classes.section}>
                <Box my={8}>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="img-thumb wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app1} alt="along app screeshot" />
                            </Box>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                <Box fontWeight="bold" letterSpacing={1}>How it works?</Box>
                            </Button>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Best Ride App</Box>
                            </Typography>
                            <Box display="block" my={1}></Box>
                            <Typography>
                                Download from your device app store <br />
                                Search and select your destination <br />
                                Request and join a ride in minutes
                            </Typography>
                            <Box display="block" my={2}></Box>
                            <Button variant="text" >
                                Download Now <ArrowRightAltIcon />
                            </Button>
                        </Grid>
                    </Grid>
                    <Box my={4} display="block"></Box>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={4}>
                            <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                <Box fontWeight="bold" letterSpacing={1}>Save Save Save</Box>
                            </Button>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Best Prices!</Box>
                            </Typography>
                            <Box display="block" my={1}></Box>
                            <Typography>
                                We offer the best ride prices, discount codes and promotions
                            </Typography>
                            <Box display="block" my={2}></Box>
                            <Button variant="text" >
                                Download Now <ArrowRightAltIcon />
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app2} alt="along app screeshot" />
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
        </>
    )
}

function Video() {
    const classes = useStyles();
    const [isOpen, setOpen] = React.useState(false)

    return (
        <>
            <Container className={classes.videoSection}>
                <Grid container justify="center" alignItems="center">
                    <Grid item>
                        <Box textAlign="center" my={8}>
                            <ModalVideo channel='youtube' autoplay isOpen={isOpen} fs videoId="HicQI2-ahqQ" onClose={() => setOpen(false)} />
                            <IconButton className="popup-youtube" onClick={()=> setOpen(true)} style={{backgroundColor: "white", color:"#4a148c", padding: "25px"}}>
                                <MovieIcon />
                            </IconButton>
                            <Box my={2}></Box>
                            <Typography className="wow zoomIn" data-wow-duration="1000ms" data-wow-delay="100ms" gutterBottom variant="h4">WATCH VIDEO</Typography>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
        </>
    )
}

function Benefits() {
    const classes = useStyles();

    return (
        <>
            <Container id="app-benefits" className={classes.secion}>
                <Grid container justify="center">
                    <Grid item>
                        <Box mt={8} textAlign="center">
                            <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                <Box fontWeight="bold" letterSpacing={1}>Benefits</Box>
                            </Button>
                            <Typography variant="h5" className={`${classes.sectionTitle} ${"wow fadeIn"}`}>
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Why use the Along app?</Box>
                            </Typography>
                        </Box>
                    </Grid>
                </Grid>
                <Grid container justify="center">
                    <Grid item>
                        <Box my={2}>
                            <BenefitsTab />
                        </Box>
                    </Grid>
                </Grid>
            </Container>
        </>
    )
}

function Testimonial() {
    const classes = useStyles();

    const items = [
        {
            name: "Segun Bello",
            title: "Passenger",
            description: "Living in Lugbe, this is a very affordable way to getting to work daily and meeting other professionals.",
        },
        {
            name: "Obinna Uzoka",
            title: "Driver",
            description: "Very good way to make som extra money daily.",
        },
        {
            name: "Hadiza Sambo",
            title: "Passenger",
            description: "It is affordable, convenient and all the advantages of a taxi at a good price",
        }
    ]

    return (
        <>
            <Container id="app-reviews" className={classes.section}  style={{backgroundColor:"#f6fbfd"}}>
                <Grid container justify="center">
                    <Grid item xs={12}>
                        <Box my={8} textAlign="center">
                            <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                <Box fontWeight="bold" letterSpacing={1}>Reviews</Box>
                            </Button>
                            <Typography variant="h5" className={`${classes.sectionTitle} ${"wow fadeIn"}`}>
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Clients Who Loved Us</Box>
                            </Typography>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Carousel animation="slide" navButtonsAlwaysInvisible="true">
                            {items.map((item, i) => (
                                <Paper elevation={3} key={i} style={{width:"100%"}}>
                                    <Box p={4} textAlign="center">
                                        <IconButton className={classes.icon}><PersonIcon /></IconButton>
                                        <Box my={3}></Box>
                                        <Typography gutterBottom variant="body2">{item.description}</Typography>
                                        <Typography gutterBottom variant="subtitle1"><b>{item.name}</b></Typography>
                                        <Typography variant="body2">{item.title}</Typography>
                                    </Box>
                                </Paper>
                            ))}
                        </Carousel>
                    </Grid>
                </Grid>
            </Container>
        </>
    )
}

function Download() {
    const classes = useStyles();
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up("sm"));
    const bmr = () => {
        if(matches) {
            return "0";
        }

        return "10px";
    }

    return (
        <>
            <Container id="app-downloads">
                <Grid container alignItems="center">
                    <Grid item xs={12} md={6} className="wow fadeInLeft" data-wow-delay="0.2s">
                        <img src={img3} width="100%" alt="along full logo" />
                    </Grid>
                    <Grid item xs={12} md={5} className="wow fadeInRight" data-wow-delay="0.2s">
                        <Box my={8}>
                            <Typography variant="h5"><b>Download Our App From Store</b></Typography>
                            <Typography variant="body2">Download Along for iPhone or Android to easily request and join a ride with the tap of a button.</Typography>
                            <Box my={2} textAlign="center">
                                <Typography variant="subtitle1" gutterBottom><b>Driver App</b></Typography>
                                <Button disableElevation style={{marginBottom: bmr(), marginRight: bmr() === "0" ? "10px" : bmr()}} className={classes.buttonContained} variant="contained" href="https://play.google.com/store/apps/details?id=com.alongng.driver"><AndroidIcon className={classes.buttonIcon}/> From PlayStore</Button>
                                <Button disableElevation className={classes.buttonOutlined} variant="outlined" href="https://apps.apple.com/us/app/along-driver/id1515804893"><AppleIcon className={classes.buttonIcon} /> From Appstore</Button>
                            </Box>
                            <Box mt={2} textAlign="center">
                                <Typography variant="subtitle1" gutterBottom><b>Passenger App</b></Typography>
                                <Button disableElevation style={{marginBottom: bmr(), marginRight: bmr() === "0" ? "10px" : bmr()}} className={classes.buttonContained} variant="contained" href="https://play.google.com/store/apps/details?id=com.alongng.app"><AndroidIcon className={classes.buttonIcon}/> From PlayStore</Button>
                                <Button disableElevation className={classes.buttonOutlined} variant="outlined" href="https://apps.apple.com/us/app/along/id1515803652"><AppleIcon className={classes.buttonIcon} /> From Appstore</Button>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
        </>
    )
}

function Contact() {
    const classes = useStyles();

    return (
        <>
            <Container className={classes.section} id="app-contact" style={{padding: 0}}>
                <object style={{border:0, height: "450px", width: "100%"}} data="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3939.7952615558406!2d7.462998814779986!3d9.082407690650983!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x104e0aef32acc231%3A0xbeefd72576abafa!2sOlive%20plaza!5e0!3m2!1sen!2suk!4v1589456432798!5m2!1sen!2suk">Along Map location</object>
                <Box p={2}>
                    <Grid container justify="center">
                        <Grid item xs={12} sm={6} md={4}>
                            <Paper elevation={3}>
                                <Box mb={4} p={4} textAlign="center">
                                    <Button className={`${classes.sectionHeader} ${"wow fadeInDown"}`}>
                                        <Box fontWeight="bold" letterSpacing={1}>Contact</Box>
                                    </Button>
                                    <Typography variant="h5" className={`${classes.sectionTitle} ${"wow fadeIn"}`}>
                                        <Box fontWeight="bold" letterSpacing={1} mt={2}>We love to hear from you</Box>
                                    </Typography>
                                    <p>Email: <Box component={Anchor} href="mailto:support@along.ng">support@along.ng</Box></p>
                                </Box>
                            </Paper>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
        </>
    )
}