import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import SendIcon from '@material-ui/icons/Send';
import bg from '../../static/images/car.jpg';

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const useStyles = makeStyles((theme) => ({
    bg: {
        position: "relative",
        backgroundImage: "linear-gradient(0deg, rgba(74, 20, 140, 0.4), rgba(171, 71, 188, 0.7)), url(" + bg + ")",
        backgroundAttachment: "fixed",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        width: "100%"
        //#4a148c #ab47bc
    },
    root: {
        padding: "0 10px",
        display: 'flex',
        alignItems: 'center',
        borderRadius: 30
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    paper: {
        position: 'absolute',
        [theme.breakpoints.down("xs")]: {
            width: 300
        },
        [theme.breakpoints.up("md")]: {
            width: 400
        },
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
    },
}));

export default function Subscription() {
    const classes = useStyles();
    // getModalStyle is not a pure function, we roll the style only on the first render
    const [modalStyle] = React.useState(getModalStyle);
    const [open, setOpen] = React.useState(false);

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpen = () => {
        if (!localStorage.getItem('subscribed')) {
            setTimeout(function () {
                setOpen(true);
            }, 10000);
        }
    };

    React.useEffect(() => {
        handleOpen();
    }, []);

    const body = (
        <>
            <div style={modalStyle} className={classes.paper}>
                <img src={bg} alt="along nigeria car.png" width="100%" />
                <Box my={5} textAlign="center">
                    <Container>
                        <Grid container>
                            <Grid item>
                                <Typography id="simple-modal-title" variant="h6" gutterBottom><b>Sign up to our Newsletter to stay up to date with our latest promos!</b></Typography>
                                <Typography id="simple-modal-description" variant="body2" gutterBottom>You get N500 off your ride when you sign up.</Typography>
                                <Box my={2}></Box>
                                <Paper component="form" onSubmit={(e) => {e.preventDefault(); localStorage.setItem('subscribed', true); handleClose();}} className={classes.root}>
                                    <InputBase
                                        type="email"
                                        required
                                        className={classes.input}
                                        placeholder="Enter your email address"
                                        inputProps={{ 'aria-label': 'enter your email address' }}
                                    />
                                    <IconButton type="submit" className={classes.iconButton} aria-label="search">
                                        <SendIcon />
                                    </IconButton>
                                </Paper>
                            </Grid>
                        </Grid>
                    </Container>
                </Box>
            </div>
        </>
    );

    return (
        <>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                {body}
            </Modal>
        </>
    );
}