import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Anchor from '@material-ui/core/Link';
import IconButton from '@material-ui/core/IconButton';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import { Link } from 'react-router-dom';
import logo from '../../static/images/along_full_logo.png';

const useStyles = makeStyles((theme) => ({
    bg: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white"
    },
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    appBar: {
        backgroundColor: "transparent",
        boxShadow: "none",
        color: "white"
    },
    anchor: {
        textDecoration: "none",
        color: "white"
    }
}));

export default function Footer() {
    const classes = useStyles();

    return (
        <>
            <Container className={classes.bg}>
                <Box px={8}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={3}>
                            <Box my={4}>
                                <img src={logo} width="50%" alt="along logo" />
                                <Typography variant="body2">Along is a ride sharing app helping replace what people currently do today "along" which is joining a ride heading in the same direction.</Typography>
                            </Box>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <Box my={4}>
                                <Typography variant="h6">Support</Typography>
                                <List dense>
                                    <ListItem style={{padding: 0}}>
                                        <ListItemText
                                            primary={<Link className={classes.anchor} to="/">Terms and Conditions</Link>}
                                        />
                                    </ListItem>
                                    <ListItem style={{padding: 0}}>
                                        <ListItemText
                                            primary={<Link className={classes.anchor} to="/">Privacy Policy</Link>}
                                        />
                                    </ListItem>
                                    <ListItem style={{padding: 0}}>
                                        <ListItemText
                                            primary={<Link className={classes.anchor} to="/how-it-works">How It works</Link>}
                                        />
                                    </ListItem>
                                    <ListItem style={{padding: 0}}>
                                        <ListItemText
                                            primary={<Link className={classes.anchor} to="/driver">Become A Driver</Link>}
                                        />
                                    </ListItem>
                                </List>
                            </Box>
                        </Grid>

                    </Grid>
                </Box>
            </Container>
            <Divider />
            <Box px={4} className={classes.bg}>
                    <AppBar position="static" className={classes.appBar}>
                        <Toolbar>
                            <Typography variant="body1" className={classes.title}>
                                &copy; {new Date().getFullYear()} Designed by <Box component={Anchor} href="http://bid0706.github.io">Ibiso Barimalaa</Box>
                            </Typography>
                            <IconButton color="inherit" href="https://instagram.com/along_nigeria" target="_blank"><InstagramIcon /></IconButton>
                            <IconButton color="inherit" href="https://twitter.com/along_nigeria" target="_blank"><TwitterIcon /></IconButton>
                        </Toolbar>
                    </AppBar>
                </Box>
        </>
    );
}