import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import RecentActorsIcon from '@material-ui/icons/RecentActors';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import DescriptionIcon from '@material-ui/icons/Description';
import LocalTaxiIcon from '@material-ui/icons/LocalTaxi';
import AndroidIcon from '@material-ui/icons/Android';
import AppleIcon from '@material-ui/icons/Apple';

import bg from '../../static/images/man-in-car.jpg';
import app1 from '../../static/images/features/img1.png';
import app2 from '../../static/images/features/img2.png';

const useStyles = makeStyles((theme) => ({
    section: {
        minHeight: "100vh"
    },
    sectionHeader: {
        backgroundColor: theme.palette.background.soft,
        color: theme.palette.text.light,
        borderRadius: "20px",
        padding: "6px 30px"
    },
    sectionTitle: {
        color: theme.palette.text.soft
    },
    bg: {
        position: "relative",
        backgroundImage: "linear-gradient(0deg, rgba(74, 20, 140, 0.4), rgba(171, 71, 188, 0.7)), url(" + bg + ")",
        backgroundAttachment: "fixed",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
        //#4a148c #ab47bc
    },
    button: {
        borderRadius: "30px",
        padding: "7px 50px",
        border: `2px solid ${theme.palette.text.light}`,
        color: theme.palette.text.light
    },
    icon: {
        backgroundImage: "linear-gradient(0deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "15px"
    },
    buttonContained: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "10px 30px",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonOutlined: {
        color: "#4a148c",
        padding: "10px 30px",
        border: "1px solid #4a148c",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonIcon: {
        marginRight: "3px"
    }
}));

const handleClick = (e) => {
    e.preventDefault();
    const anchor = document.querySelector(e.currentTarget.getAttribute("href"));
    if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
};

export default function Driver() {
    const [expanded, setExpanded] = React.useState("panel1");

    const handleAccordion = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const classes = useStyles();
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up("sm"));
    const bmr = () => {
        if (matches) {
            return "20px";
        }

        return "0";
    }

    const fs = () => {
        if (matches) {
            return "h3";
        }

        return "h5";
    }

    const tc = () => {
        if (matches) {
            return "left";
        }

        return "center";
    }

    return (
        <>
            <Container className={classes.bg}>
                <Grid container className={classes.section} justify="center" alignItems="center">
                    <Grid item xs={10} md={6}>
                        <Typography
                            variant={fs()}
                            gutterBottom
                            style={{
                                marginLeft: "-3px",
                                color: "white",
                                animatinoDelay: "0.2s",
                                MozAnimationDelay: "0.2s",
                                WebkitAnimationDelay: "0.2s"
                            }}
                            className={`${"wow fadeInRight"}`}>
                            <Box textAlign={tc()} fontWeight="bold">Know what you need to drive with Along</Box>
                        </Typography>
                        <Typography
                            variant="subtitle1"
                            style={{ color: "white", animatinoDelay: "0.3s", MozAnimationDelay: "0.3s", WebkitAnimationDelay: "0.3s" }}
                            className={`${"wow fadeInRight"}`}>
                            <Box textAlign={tc()}>Easy steps you need to know</Box>
                        </Typography>
                        <Box mt={3} textAlign={tc()}>
                            <Button
                                variant="contained"
                                href="#learn-driver"
                                onClick={handleClick}
                                className={`${classes.button} ${"wow rubberBand"}`}
                                style={{ marginRight: bmr(), color: theme.palette.text.main, backgroundColor: theme.palette.background.default, animatinoDelay: "0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s" }}
                                disableElevation
                            ><b>Learn more</b></Button>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Container id="learn-driver">
                <Grid container alignItems="center" justify="center" className={classes.section}>
                    <Grid item>
                        <Box my={8}>
                            <Grid container justify="center" alignItems="center" spacing={2}>
                                <Grid item>
                                    <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><RecentActorsIcon /></IconButton>
                                </Grid>
                                <Grid item xs={12} md={8} className="wow slideInRight" data-wow-delay="0.3s">
                                    <Typography variant="subtitle1"><b>Your driver's license</b></Typography>
                                    <Typography variant="body2">Drivers must meet the age requirement fro driving in Nigeria</Typography>
                                </Grid>
                            </Grid>
                            <Box my={3}></Box>
                            <Grid container justify="center" alignItems="center" spacing={2}>
                                <Grid item>
                                    <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><AssignmentTurnedInIcon /></IconButton>
                                </Grid>
                                <Grid item xs={12} md={8} className="wow slideInRight" data-wow-delay="0.3s">
                                    <Typography variant="subtitle1"><b>A background check</b></Typography>
                                    <Typography variant="body2">We will do a complete background check on you and review your driving record.
                                </Typography>
                                </Grid>
                            </Grid>
                            <Box my={3}></Box>
                            <Grid container justify="center" alignItems="center" spacing={2}>
                                <Grid item>
                                    <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><DescriptionIcon /></IconButton>
                                </Grid>
                                <Grid item xs={12} md={8} className="wow slideInRight" data-wow-delay="0.3s">
                                    <Typography variant="subtitle1"><b>Insurance and other documents</b></Typography>
                                    <Typography variant="body2">Submit your a copy of your vehicle insurance, roadworthiness certificate, and plate number.
                                </Typography>
                                </Grid>
                            </Grid>
                            <Box my={3}></Box>
                            <Grid container justify="center" alignItems="center" spacing={2}>
                                <Grid item>
                                    <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><LocalTaxiIcon /></IconButton>
                                </Grid>
                                <Grid item xs={12} md={8} className="wow slideInRight" data-wow-delay="0.3s">
                                    <Typography variant="subtitle1"><b>Car or Rental</b></Typography>
                                    <Typography variant="body2">You must have a 4 door car with a minimum requirement of 2003 model.
                                </Typography>
                                </Grid>
                            </Grid>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Container className={classes.section} style={{ backgroundColor: "#f6fbfd" }}>
                <Box py={8}>
                    <Typography variant="h5" className={`${classes.sectionTitle} ${"wow fadeIn"}`}>
                        <Box fontWeight="bold" textAlign="center" letterSpacing={1} mt={2}>Onboarding Process</Box>
                    </Typography>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="img-thumb wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app1} alt="along app screeshot" />
                            </Box>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Create an Account</Box>
                            </Typography>
                            <Box my={1}></Box>
                            <Typography>
                                Download from your device app store
                            </Typography>
                            <Box my={2}></Box>
                            <Button disableElevation style={{ marginBottom: "10px", marginRight: bmr() === "0" ? "10px" : bmr() }} className={classes.buttonContained} variant="contained" href="https://play.google.com/store/apps/details?id=com.alongng.driver"><AndroidIcon className={classes.buttonIcon} /> From PlayStore</Button>
                            <Button disableElevation style={{ marginBottom: "10px" }} className={classes.buttonOutlined} variant="outlined" href="https://apps.apple.com/us/app/along-driver/id1515804893"><AppleIcon className={classes.buttonIcon} /> From Appstore</Button>
                        </Grid>
                    </Grid>
                    <Box my={4} display="block"></Box>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={4}>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Driver Information</Box>
                            </Typography>
                            <Box my={1}></Box>
                            <Typography>
                                Complete your driver information on the app. Tell us about yourself, upload your driver's license and give us some information about your car
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app2} alt="along app screeshot" />
                            </Box>
                        </Grid>
                    </Grid>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="img-thumb wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app1} alt="along app screeshot" />
                            </Box>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Inspection</Box>
                            </Typography>
                            <Box my={1}></Box>
                            <Typography>
                                Visit one of our inspection location and come along with your vehicle insurance, roadworthiness certification and driver's license.
                            </Typography>
                        </Grid>
                    </Grid>
                    <Box my={4} display="block"></Box>
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={12} md={4}>
                            <Typography variant="h5" className="wow fadeIn">
                                <Box fontWeight="bold" letterSpacing={1} mt={2}>Get Approved and Earn</Box>
                            </Typography>
                            <Box my={1}></Box>
                            <Typography>
                                Test your driving skills, provide your shipping address/visit or office address to pick up your good bag. Wait for your application to be approved then start driving and earn.
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Box display="block" className="wow fadeInLeft" data-wow-delay="0.3s">
                                <img width="100%" src={app2} alt="along app screeshot" />
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
            <Container>
                <Box my={8}>
                    <Grid container justify="center">
                        <Grid item xs={12} md={6}>
                            <Accordion expanded={expanded === 'panel1'} onChange={handleAccordion('panel1')}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel1a-content"
                                    id="panel1a-header"
                                >
                                    <Typography>Car Type Allowed</Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <Typography variant="body2">
                                        Our minimum requirement is a 2003 model. All cars have to pass inspection and document verification before accepting passengers
                                    </Typography>
                                </AccordionDetails>
                            </Accordion>
                            <Accordion expanded={expanded === 'panel2'} onChange={handleAccordion('panel2')}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel2a-content"
                                    id="panel2a-header"
                                >
                                    <Typography>Inspection Points</Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <Typography variant="body2">
                                        Total JS Tarka Service Station, beside Gadeb plaza, Area 3, Garki, Abuja FCT. <br /><br />
                                        Total Wuse 1 Service Station, Wuse zone 5, Abuja FCT. <br /><br/>
                                        Tf06, 3rd floor, Olive plaza, Banex, Ahmadu Bello way.
                                    </Typography>
                                </AccordionDetails>
                            </Accordion>
                            <Accordion expanded={expanded === 'panel3'} onChange={handleAccordion('panel3')}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel2a-content"
                                    id="panel3a-header"
                                >
                                    <Typography>Costs</Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <Typography variant="body2">
                                        The cost of inspection requires a sum of <b>NGN500</b> only.
                                    </Typography>
                                </AccordionDetails>
                            </Accordion>
                            <Accordion expanded={expanded === 'panel4'} onChange={handleAccordion('panel4')}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel2a-content"
                                    id="panel4a-header"
                                >
                                    <Typography>Pay out</Typography>
                                </AccordionSummary>
                                <AccordionDetails>
                                    <Typography variant="body2">
                                        Pay outs are done on the first business day of the new week to your account. Ensure that you make your payout request before the last business day.
                                    </Typography>
                                </AccordionDetails>
                            </Accordion>
                        </Grid>
                    </Grid>
                </Box>
            </Container>
        </>
    )
}