import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import SportsMotorsportsIcon from '@material-ui/icons/SportsMotorsports';
import AirlineSeatReclineNormalIcon from '@material-ui/icons/AirlineSeatReclineNormal';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ChatIcon from '@material-ui/icons/Chat';
import DriveEtaIcon from '@material-ui/icons/DriveEta';
import Button from '@material-ui/core/Button';
import AndroidIcon from '@material-ui/icons/Android';
import AppleIcon from '@material-ui/icons/Apple';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import { Link } from 'react-router-dom';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
        minHeight: "100vh",
    },
    icon: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "15px"
    },
    buttonContained: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "10px 30px",
        borderRadius: "30px",
        marginBottom: "10px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonOutlined: {
        color: "#4a148c",
        padding: "10px 30px",
        border: "1px solid #4a148c",
        borderRadius: "30px",
        marginBottom: "10px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonIcon: {
        marginRight: "3px"
    }
}));

export default function DriverTab() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <Tabs
                variant="fullWidth"
                value={value}
                onChange={handleChange}
                aria-label="Vertical tabs"
                indicatorColor="primary"
                textColor="primary"
            >
                {/* Driver */}
                <Tab label="Driver" icon={<SportsMotorsportsIcon />} {...a11yProps(0)} />
                {/* Passenger */}
                <Tab label="Passenger" icon={<AirlineSeatReclineNormalIcon />} {...a11yProps(1)} />
            </Tabs>
            <Divider />
            <TabPanel value={value} index={0} >
                <Grid container justify="center" spacing={2}>
                    <Grid item xs={12} md={4}>
                        <List dense>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Cheaper Running Costs"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Driver Controlled Prices"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Referral &amp; Loyalty Points Rewards"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Total Fuel Card"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="2% Cashback on Monthly Rides"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Free Data Usage on MTN"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Female Only Rides Selection for Fermale Drivers"
                                />
                            </ListItem>
                        </List>
                    </Grid>
                    <Grid item xs={12} md={5}>
                        <Box mb={2}>
                            <Grid container spacing={1}>
                                <Grid item xs={12} md={2}>
                                    <IconButton className={classes.icon}>
                                        <ChatIcon />
                                    </IconButton>
                                </Grid>
                                <Grid item xs={12} md={9}>
                                    <Typography variant="h5" gutterBottom>
                                        <b>Interact With Passengers in Real-time</b>
                                    </Typography>
                                    <Typography variant="body2">
                                        With Along, both passengers and drivers can look at the user profile, reviews, chat with the passenger or driver, see the cost of the ride, the destination and pay for the ride. Both parties would have created an understanding and the need to carry cash will not be required.
                                    </Typography>
                                </Grid>
                            </Grid>
                            <Box my={2} display="block"></Box>
                            <Grid container spacing={1}>
                                <Grid item xs={12} md={2}>
                                    <IconButton className={classes.icon}>
                                        <DriveEtaIcon />
                                    </IconButton>
                                </Grid>
                                <Grid item xs={12} md={9}>
                                    <Typography variant="h5" gutterBottom>
                                        <b>Turn your daily route into an income stream</b>
                                    </Typography>
                                    <Typography variant="body2">
                                        On your daily commute from Lugbe to Wuse Zone 2 or a weekend long distance travel from Abuja to Lokoja, "Along" lets you set pricing per seat, choose the best location to pickup passengers and evaluate safety on pickup while earning some cash on these trips.
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <Box my={2}>
                            <Typography variant="subtitle1"><b>Download for Driver</b></Typography>
                            <Button disableElevation className={classes.buttonContained} variant="contained" href="https://play.google.com/store/apps/details?id=com.alongng.driver"><AndroidIcon className={classes.buttonIcon}/> From PlayStore</Button>
                            <Box component="span" mx={1}></Box>
                            <Button disableElevation className={classes.buttonOutlined} variant="outlined" href="https://apps.apple.com/us/app/along-driver/id1515804893"><AppleIcon className={classes.buttonIcon} /> From Appstore</Button>
                        </Box>
                    </Grid>
                </Grid>
                <Grid container justify="center">
                    <Grid item>
                        <Box textAlign="center">
                            <Button compenent={Link} to="/driver" variant="text" >
                                Learn more <ArrowRightAltIcon />
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
            </TabPanel>
            <TabPanel value={value} index={1}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={4}>
                        <List dense>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Cheaper &amp; Safe Travel Options"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Verified Drivers"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Referral &amp; Loyalty Points Rewards"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Free Data Usage on MTN"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <ArrowForwardIosIcon />
                                </ListItemIcon>
                                <ListItemText
                                    primary="Female Only Ride Selection"
                                />
                            </ListItem>
                        </List>
                        <Typography variant="body2">All drivers are verified there by helping mitigate the security concerns to an acceptable level.</Typography>
                    </Grid>
                    <Grid item xs={12} md={5}>
                        <Box mb={2}>
                            <Grid container spacing={1}>
                                <Grid item xs={12} md={2}>
                                    <IconButton className={classes.icon}>
                                        <ChatIcon />
                                    </IconButton>
                                </Grid>
                                <Grid item xs={12} md={9}>
                                    <Typography variant="h5" gutterBottom>
                                        <b>Interact With Drivers in Real-time</b>
                                    </Typography>
                                    <Typography variant="body2">
                                        When you join a ride on the motorway, you must negotiate price, evaluate safety, understand and explain to the driver your drop off point. If this needs are not met in 1 - 2 minutes, the whole process is repeat over and over again. With Along, both passengers and drivers can look at the user profile, reviews, chat with the passenger or driver, see the cost of the ride, the destination and pay for the ride. Both parties would have created an understanding and the need to carry cash will not be required.
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Box>
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <Box my={2}>
                            <Typography variant="subtitle1"><b>Download for Passenger</b></Typography>
                            <Button disableElevation className={classes.buttonContained} variant="contained" href="https://play.google.com/store/apps/details?id=com.alongng.app"><AndroidIcon className={classes.buttonIcon}/> From PlayStore</Button>
                            <Box component="span" mx={1}></Box>
                            <Button disableElevation className={classes.buttonOutlined} variant="outlined" href="https://apps.apple.com/us/app/along/id1515803652"><AppleIcon className={classes.buttonIcon} /> From Appstore</Button>
                        </Box>
                    </Grid>
                </Grid>
            </TabPanel>
        </div>
    );
}