import React from 'react';
import clsx from 'clsx';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Hidden from '@material-ui/core/Hidden';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import NotesIcon from '@material-ui/icons/Notes';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import { Link } from 'react-router-dom';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import CloseIcon from '@material-ui/icons/Close';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import logo from '../../static/images/logo.png';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        background: "rgba(0,0,0,0.4)",
        boxShadow: "none"
    },
    title: {
        position: "relative",
        flexGrow: 1,
    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,

    },
    drawerPaper: {
        width: drawerWidth,
        background: "rgba(0,0,0,0.77)"
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-end',
        color: "white",
    },
    button: {
        textTransform: "none",
    },
    st0: { 
        fill: "white",
        
    }
}));

const NavItem = [
    ["Features", "#app-features"],
    ["Benefits", '#app-benefits'],
    ["Testimonial", "#app-reviews"],
    ["Download", "#app-downloads"],
    ["Contact", "#app-contact"]
]

const NotNavItem = [
    ["How It Works", "/how-it-works"],
    ["Driver", "/driver"]
]

export default function Header(props) {
    const {home} = {...props};

    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const childProps = {
        open,
        home,
        handleDrawerClose,
        handleDrawerOpen
    }


    return (
        <>
            <CssBaseline />
            <Main {...childProps} />
            <Sidebar {...childProps} />       
        </>
    )
}

const handleClick = (e) => {
    e.preventDefault();
    const anchor = document.querySelector(e.currentTarget.getAttribute("href"));
    if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
};

const Main = (props) => {
    const classes = useStyles();
    

    let index = 0;
    return (
        <>
            <AppBar position="fixed" className={classes.appBar}>
                    <Toolbar>
                        <Hidden smUp>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                onClick={props.handleDrawerOpen}
                                edge="start"
                                className={clsx(classes.menuButton, props.open && classes.hide)}
                            >
                                <NotesIcon />
                            </IconButton>
                        </Hidden>
                        <Box className={`${classes.title}`}>
                            <img src={logo} width="45px" alt="logo.png" />
                        </Box>
                        <Hidden xsDown>
                            <Button
                                key="home"
                                color="inherit"
                                component={Link}
                                to="/"
                                className={`${classes.button}`}
                                style={{ animationDelay: 0 }}
                            >Home</Button>
                            {
                                props.home && 
                                NavItem.map(([value, key]) => {
                                    index += 0.1;
                                    return (
                                        <Button 
                                            key={value}
                                            color="inherit"
                                            href={key}
                                            onClick={handleClick}
                                            className={`${classes.button}`}
                                            style={{animationDelay: `${index + "s"}`}}
                                        >{value}</Button>
                                    )
                                })
                            }
                            {
                                !props.home && 
                                NotNavItem.map(([value, key]) => {
                                    index += 0.1;
                                    return (
                                        <Button 
                                            key={value}
                                            color="inherit" 
                                            component={Link} 
                                            to={key} 
                                            className={`${classes.button}`}
                                            style={{animationDelay: `${index + "s"}`}}
                                        >{value}</Button>
                                    )
                                })
                            }
                        </Hidden>
                    </Toolbar>
                </AppBar>           
        </>
    )
}

const Sidebar = (props) => {
    const classes = useStyles();

    return (
        <Hidden smUp>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={props.open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <IconButton onClick={props.handleDrawerClose} color="inherit">
                        <CloseIcon />
                    </IconButton>
                </div>
                <Divider />
                <List>
                    <ListItem onClick={props.handleDrawerClose} button key="home" component={Link} to="/">
                        <ListItemText style={{ color: "white" }} primary="Home" />
                    </ListItem>
                    {props.home && NavItem.map(([text, index]) => (
                        <ListItem onClick={(e) => {handleClick(e); props.handleDrawerClose()}} button key={text} href={index}>
                            <ListItemText style={{ color: "white" }} primary={text} />
                        </ListItem>
                    ))}
                    {!props.home && NotNavItem.map(([text, index]) => (
                        <ListItem onClick={props.handleDrawerClose} button key={text} component={Link} to={index}>
                            <ListItemText style={{ color: "white" }} primary={text} />
                        </ListItem>
                    ))}
                </List>
            </Drawer>
        </Hidden>
    )
}