import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import SearchIcon from '@material-ui/icons/Search';
import DoneAllIcon from '@material-ui/icons/DoneAll';
import PersonPinCircleIcon from '@material-ui/icons/PersonPinCircle';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import AirportShuttleIcon from '@material-ui/icons/AirportShuttle';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import CreditCardIcon from '@material-ui/icons/CreditCard';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import Carousel from 'react-material-ui-carousel';

import bg from '../../static/images/man-texting.jpg';
import bg1 from '../../static/images/woman-driving.jpeg';
import { IconButton } from '@material-ui/core';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

const useStyles = makeStyles((theme) => ({
    carousel: {
        backgroundImage: "linear-gradient(0deg, rgba(74, 20, 140, 0.4), rgba(171, 71, 188, 0.7))",
    },
    bg: {
        position: "relative",
        backgroundImage: "linear-gradient(0deg, rgba(74, 20, 140, 0.4), rgba(171, 71, 188, 0.7)), url(" + bg + ")",
        backgroundAttachment: "fixed",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
        //#4a148c #ab47bc
    },
    bg1: {
        position: "relative",
        backgroundImage: "linear-gradient(0deg, rgba(74, 20, 140, 0.4), rgba(171, 71, 188, 0.7)), url(" + bg1 + ")",
        backgroundAttachment: "fixed",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
        //#4a148c #ab47bc
    },
    section: {
        minHeight: "100vh"
    },
    sectionHeader: {
        backgroundColor: theme.palette.background.soft,
        color: theme.palette.text.light,
        borderRadius: "20px",
        padding: "6px 30px"
    },
    icon: {
        backgroundImage: "linear-gradient(0deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "15px"
    },
    button: {
        borderRadius: "30px",
        padding: "7px 50px",
        border: `2px solid ${theme.palette.text.light}`,
        color: theme.palette.text.light
    },
    videoSection: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white"
    },
    buttonContained: {
        backgroundImage: "linear-gradient(90deg, #4a148c, #ab47bc)",
        color: "white",
        padding: "10px 30px",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonOutlined: {
        color: "#4a148c",
        padding: "10px 30px",
        border: "1px solid #4a148c",
        borderRadius: "30px",
        fontWeight: theme.typography.fontWeightBold,
        letterSpacing: 1
    },
    buttonIcon: {
        marginRight: "3px"
    }
}));

const handleClick = (e) => {
    e.preventDefault();
    const anchor = document.querySelector(e.currentTarget.getAttribute("href"));
    if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
};

export default function HowItWorks() {
    const classes = useStyles();
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up("sm"));
    const bmr = () => {
        if (matches) {
            return "20px";
        }

        return "0";
    }

    const fs = () => {
        if (matches) {
            return "h3";
        }

        return "h5";
    }

    const tc = () => {
        if (matches) {
            return "left";
        }

        return "center";
    }

    const [value, setValue] = React.useState(0);
    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <>
            <CssBaseline />
            <Carousel className={classes.carousel} onChange = {(index) => handleChange(index)} navButtonsAlwaysVisible={true} autoPlay={false} indicators={false} animation="slide">
                <Container className={classes.bg}>
                    <Grid container className={classes.section} justify="center" alignItems="center">
                        <Grid item xs={10}>
                            <Typography
                                variant={fs()}
                                gutterBottom
                                style={{
                                    marginLeft: "-3px",
                                    color: "white",
                                    animatinoDelay: "0.2s",
                                    MozAnimationDelay: "0.2s",
                                    WebkitAnimationDelay: "0.2s"
                                }}
                                className={`${"wow fadeInRight"}`}>
                                <Box textAlign={tc()} fontWeight="bold">Need a ride?</Box>
                            </Typography>
                            <Typography
                                variant="subtitle1"
                                style={{ color: "white", animatinoDelay: "0.3s", MozAnimationDelay: "0.3s", WebkitAnimationDelay: "0.3s" }}
                                className={`${"wow fadeInRight"}`}>
                                <Box textAlign={tc()}>Find a trusted driver on along.ng</Box>
                            </Typography>
                            <Box mt={3} textAlign={tc()}>
                                <Button
                                    variant="contained"
                                    href="#how-pax"
                                    onClick={handleClick}
                                    className={`${classes.button} ${"wow rubberBand"}`}
                                    style={{ marginRight: bmr(), color: theme.palette.text.main, backgroundColor: theme.palette.background.default, animatinoDelay: "0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s" }}
                                    disableElevation
                                ><b>Learn more</b></Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Container>
                <Container className={classes.bg1}>
                    <Grid container className={classes.section} justify="center" alignItems="center">
                        <Grid item xs={10}>
                            <Typography
                                variant={fs()}
                                gutterBottom
                                style={{
                                    marginLeft: "-3px",
                                    color: "white",
                                    animatinoDelay: "0.2s",
                                    MozAnimationDelay: "0.2s",
                                    WebkitAnimationDelay: "0.2s"
                                }}
                                className={`${"wow fadeInRight"}`}>
                                <Box textAlign={tc()} fontWeight="bold">Have an Empty Seat?</Box>
                            </Typography>
                            <Typography
                                variant="subtitle1"
                                style={{ color: "white", animatinoDelay: "0.3s", MozAnimationDelay: "0.3s", WebkitAnimationDelay: "0.3s" }}
                                className={`${"wow fadeInRight"}`}>
                                <Box textAlign={tc()}>Offer a ride to Passengers on along.ng</Box>
                            </Typography>
                            <Box mt={3} textAlign={tc()}>
                                <Button
                                    variant="contained"
                                    href="#how-driver"
                                    onClick={handleClick}
                                    className={`${classes.button} ${"wow rubberBand"}`}
                                    style={{ marginRight: bmr(), color: theme.palette.text.main, backgroundColor: theme.palette.background.default, animatinoDelay: "0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s" }}
                                    disableElevation
                                ><b>Learn more</b></Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Container>
            </Carousel>
            <TabPanel id="how-pax" value={value} index={0} >
                <Container className={classes.section}>
                    <Box my={8}>
                        <Grid mb={2} container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><PersonAddIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Sign Up</b></Typography>
                                <Typography variant="body2">Securely register and log into your account.</Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><SearchIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Search for rides</b></Typography>
                                <Typography variant="body2">Just say where youre heading, where youre leaving from and when. Then pick a ride that works for you! If you need more info, you can message drivers before select a ride.
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><DoneAllIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Book a ride</b></Typography>
                                <Typography variant="body2">Tap request a ride. Once you do, youll have the drivers phone number to get in touch.
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><PersonPinCircleIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Enter Along</b></Typography>
                                <Typography variant="body2">Enjoy a hassle free ride with the comfort of a dedicated taxi
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><CreditCardIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Pay and review</b></Typography>
                                <Typography variant="body2">Pay for your ride, and leave a review of your driver.
                                </Typography>
                            </Grid>
                        </Grid>
                    </Box>
                </Container>
            </TabPanel>
            <TabPanel id="how-driver" value={value} index={1} >
                <Container className={classes.section}>
                    <Box my={8}>
                        <Grid container justify="center" alignItems="center">
                            <Grid item xs={12} md={4}>
                                <Typography variant="subtitle1">
                                    <Box fontWeight="fontWeightBold" textAlign="center">
                                        Share your city-to-city car journeys with trusted passengers and share your costs!
                                    </Box>
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><PersonAddIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Sign Up</b></Typography>
                                <Typography variant="body2">Securely register and log into your account.</Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><AddCircleOutlineIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Offer rides</b></Typography>
                                <Typography variant="body2">Just say where youre heading, where youre leaving from and when. Then pick a ride that works for you! If you need more info, you can message drivers before select a ride.
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><DoneAllIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Wait for requests</b></Typography>
                                <Typography variant="body2">Accept rides when you receive a request. Once you do, youll have the passenger's phone number to get in touch.
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><AirportShuttleIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Travel together</b></Typography>
                                <Typography variant="body2">Meet and ride together on your daily route with passengers
                                </Typography>
                            </Grid>
                        </Grid>
                        <Box my={3}></Box>
                        <Grid container justify="center" alignItems="center" spacing={2}>
                            <Grid item>
                                <IconButton className={`${classes.icon} ${"wow rollIn"}`} data-wow-delay="0.3s"><AccountBalanceIcon /></IconButton>
                            </Grid>
                            <Grid item xs={12} md={3} className="wow slideInRight" data-wow-delay="0.3s">
                                <Typography variant="subtitle1"><b>Earn and review</b></Typography>
                                <Typography variant="body2">Well send you your money after the ride to your Along wallet
                                </Typography>
                            </Grid>
                        </Grid>
                    </Box>
                </Container>
            </TabPanel>
        </>
    )
}