import React from 'react';
import * as Template from './Components/Layout';
import * as Views from './Components/Views';
import { HashRouter } from 'react-router-dom';
import { Route, Switch } from 'react-router-dom';
import WOW from 'wow.js';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';

export default function App() {
  const theme = createMuiTheme({
    typography: {
      fontFamily: ["Poppins", "Roboto", "Helvitica", "sans-serif", "Arial"].join(',')
    },
    palette: {
      background: {
        default: '#fff',
        main: '#4a148c',
        soft: '#ab47bc'
      },
      text: {
        main: '#4a148c',
        soft: '#ab47bc',
        light: '#fff'
      }
    }
  })
  React.useEffect(() => {
    const spinner = document.querySelector(".spinner");

    if(spinner) {
      spinner.remove();
    }

  }, [])

  React.useEffect(() => {
    const wow = new WOW();
    wow.init()
  }, []);

  return (
    <HashRouter>
      <ThemeProvider theme={theme}>
      <Template.ScrollToTop />
      
      <Template.Content>
        <Switch>
          <Route exact path="/">
            <Template.Header home={true} />
            <Views.Home />
          </Route>
          <Route path="/driver">
          <Template.Header />
            <Views.Driver />
          </Route>
          <Route path="/how-it-works">
            <Template.Header />
            <Views.HowItWorks />
          </Route>
          <Route component={Views.NotFound} />
        </Switch>
      </Template.Content>
      <Template.Footer />
      </ThemeProvider>
    </HashRouter>
  );
}

